/* 6. Write CSS rules that cause the header and .collage div to be centered on the page */
header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
}
/* 7. Write CSS rules that ensure that no more than 4 photos appear on a row */

.collage{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: center;
        justify-content: center;
}

.collage img {
    margin: 20px;
    -ms-flex-preferred-size: 20%;
        flex-basis: 20%;
}


